@import './_variables';

.search{
  display: flex;
  justify-content: center;
  // height:20vh;
  margin-top:5rem;
  & .input-wrapper{
    position: relative;
    width:30%;
    & label{
      color:#fff;
    }
    & .input{
      width:100%;
      border: 0; 
      padding: 4px 0;
      font-size:1.5rem; 
      border-bottom: 2px solid $grey_color; 
      background-color: transparent;
      color:$white_color;
      &:focus ~ .focus-border{
        width: 100%; 
        transition: 0.4s; 
        left: 0;
      }
      &:focus ~ label,
      & ~ label{
        top: -16px; 
        font-size: 12px; 
        color: $yellow_color; 
        transition: 0.3s;
      }
      & ~ label{
        position: absolute;
        left: 0;
        width: 100%; 
        top:20%; 
        color: $grey_color;
        font-size:1.3rem;
        font-weight: 200; 
        transition: 0.3s;
        z-index: -1;
        letter-spacing: 0.5px;
      }

      & ~ .focus-border{
        position: absolute; 
        bottom: 0; 
        left: 50%; 
        width: 0; 
        height: 2px; 
        background-color: $yellow_color; 
        transition: 0.4s;
      }
    }
    
  }
}

